.capa{
    background-image: url(../../../../public/assets/capa.png);
    background-repeat: no-repeat;
    background-position: center;
    object-fit: cover;
    height: 100vh;
    display: flex;
    align-items: center;
}

.capa .card-texto{
    color: white;
    display: flex;
    width: 100%;
    max-width: 1024px;
    margin: 0px auto;
    flex-direction: column;
    height: 50%;
    justify-content: center;
}

.capa p{
    font-size: 25px;
    font-weight: bold;
}

.capa .paragrafo-maior{
    font-size: 40px;
    font-weight: bolder;
    width: 30%;
    font-family: 'Anybody', cursive;
}
@import url('https://fonts.googleapis.com/css2?family=Anybody:wght@100&family=Poppins:wght@100&family=Raleway:ital,wght@1,200&display=swap');

@media (max-width:1100px) {
    .capa .card-texto{
        width: 100%;
        padding-left: 15px;
    }
}

@media (max-width:620px){
    .capa{
        background-image: url(../../../../public/assets/capa.png);
        background-position: 89% ;
        object-fit: cover;
    }

   .capa .card-texto{
    background-color: (0,0,0,6);
    }
}