header{
    width: 100%;
    padding: 10px 15px 10px;
    height: 60px;
    display: flex;
}

.topo{
    width: 100%;
    max-width: 1024px;
    display: flex;
    margin: 0px auto;
    justify-content: space-around;
    align-items: center;
}

.logo{
    width: 100%;
    height: 100%;
    display: flex;
}

a{
    text-decoration: none;
    padding-left: 10px;
    font-size: 18px;
    color: black;
    font-weight: bold;
}


@media (max-width:620px){
    .topo{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin: 20px;
    }

    .logo{
        display: flex;
        justify-content: center;
        padding-bottom: 10px;
        border-bottom: 1px solid black;

    }

    .logo img{
        width: 30%;
    }

    .link{
        display: flex;
        justify-content: space-around;
        width: 100%;
        padding-top: 10px;
    }

    header{
        height: 100px;
    }
}