#sobre{
    padding: 100px 20px 100px;
    background-color: rgba(246,246,246,255);
}


#sobre .card{
    width: 35%;
    border: 1px solid grey;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    text-align: center;
    justify-content: center;
    align-items: center;
}

.card-sobre img{
    width: 100%;
    height: 100%;
}

.card-sobre{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
    padding: 25px;
}

.card-pai{
    width: 100%;
    max-width: 1024px;
    display: flex;
    flex-direction: column;
    margin: 0px auto;
    text-align: center;
}

.card-sobre h2{
    font-size: 36px;
    color: black;
    font-weight: bold;
}

.card-sobre p{
    font-size: 18px;
    color: black;
    font-weight: bolder;
}

.texto-sobre{
    width: 95%;
    text-align: center;
    margin: 0px auto;
}

.texto-sobre p{
    font-size: 16px;
    color: black;
    font-weight: bolder;
}

.texto-sobre h1{
    font-size: 36px;
    color: black;
    font-weight: bolder;
}

@media (max-width:540px){
    .card-sobre .card{
        display: flex;
        flex-direction: column;
        width: 100%;
    }

    .card h2{
        font-size: 30px;
    }
}