#contato{
    width: 100%;
    background-color: #05031b;
    color: white;
    height: 75vh;
}

.info{
    width: 100%;
    height: 100%;
    max-width: 1024px;
    margin: 0px auto;
}

.card-contato{
    width: 40%;
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 10px;
}

.card-contato div{
    display: flex;
    width: 100%;
    height: 100%;
}

.contato-flex{
    display: flex;
    margin: 0px auto;
    justify-content:flex-end;
    align-items: flex-end;
    width: 100%;
}

.contato-flex img{
    width: 20px;
    height: 20px;
    margin-top: 20px;
    margin-right: 10px;
}

.contato-texto{
    text-align: center;
    width: 100%;
    margin: 0px auto 20px;
    padding-top: 30px;
}

.contato-texto h1{
    font-size: 36px;
    font-weight: bolder;
}

.contato-texto p{
    font-size: 18px;
    font-weight: bolder;
}

@media (max-width:620px){
    .contato-flex{
        display: flex;
        flex-direction: column;
        width: 100%;
        justify-content: center;
        align-items: center;
    }

    #contato{
        display: flex;
        width: 100%;
        height: 100%;
    }
}