#produto{
    width: 100%;
}

.card-master{
    width: 100%;
    max-width: 1024px;
    margin: 20px auto 10px;
    display: flex;
    flex-direction: column;
    text-align: center;
}
#produto h1{
    font-size: 36px;
    font-weight: 900px;
}

.card-master p{
    width: 90%;
    text-align: center;
    margin: 0px auto;
    font-size: 18px;
    margin-bottom: 10px;
    color: black;
    font-weight: bolder;
}

.lista{
    width: 100%;
    margin: 25px auto 0px;
    text-align: center;

}

.card-master .card{
    width: calc(45%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 15px;
    background-color: rgba(246,246,246,255);
}

.card-info{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 25px;
}

.card-master img{
width: 80%;
height: 160px;
}

.lista ul{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-weight: bolder;
    color: black;
}

@import url('https://fonts.googleapis.com/css2?family=Anybody:wght@100&family=Poppins:wght@100&family=Raleway:ital,wght@1,200&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Anybody:wght@100&family=Poppins:wght@100&family=Raleway:ital,wght@1,200&display=swap');

@media (max-width: 768px){
    .card-master img{
        width: 50%;
        height: 50%;
    }
}

@media (max-width: 620px){
    .card-info .card{
        width: 100%;
        display: flex;
    }
}